.profile{
  &-block{
    &__wrapper{
      max-width: 850px;

      background: #F8F8F8;
      border-radius: 25px;

      padding: 32px 110px;

      margin: 0 auto;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 24px 150px;
      &-item{
        position: relative;
        &_row{
          &-big{
            grid-row-end: span 2;
          }
        }
      }
      &-content{
        position: relative;
        width: max-content;
      }
    }
    &__help{
      position: absolute;
      width: 20px;
      height: 20px;

      right: -36px;
      top: calc(50% - 10px);
    }
    &__title{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;

      color: #2F2F2F;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 600;
      font-size: 16px;
      line-height: 16px;

      color: #2F2F2F;
      a{
        margin: 0 0 0 24px;
      }
    }
    &__title + &__text{
      margin: 4px 0 0 0;
    }
    &__link{
      width: 240px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      padding: 12px 16px;

      background: #FFFFFF;
      border-radius: 10px;

      margin: 12px 0 0 0;
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;

        max-width: 170px;
        width: 100%;

        overflow: hidden;

        text-overflow:ellipsis;
      }
      &-item{
        display: block;
        width: 22px;
        height: 22px;

        flex-shrink: 0;

        margin: 0 0 0 20px;
      }
    }
  }
  &-checkbox{
    display: flex;
    align-items: center;
    &:hover{
      cursor: pointer;
    }
    &__input{
      display: none;
      &:checked ~ .profile-checkbox__checkmark{
        background: #46C35A;
        justify-content: flex-end;
        &::before{
          left: 50%;
        }
      }
    }
    &__label{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #2F2F2F;

      &-bold{
        font-family: 'Gilroy', sans-serif;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
      }
    }
    &__checkmark{
      flex-shrink: 0;

      width: 40px;
      height: 22px;
      background: #C7C7C7;
      box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.1);
      border-radius: 14.5px;

      margin: 0 0 0 14px;

      padding: 2px;

      transition: .3s background-color linear;

      position: relative;
      &::before{
        content: '';
        position: absolute;
        left: 2px;
        top: 2px;
        width: 18px;
        height: 18px;
        display: block;

        background: #FFFFFF;
        box-shadow: 1px 0px 4px rgba(0, 0, 0, 0.2);
        border-radius: 50%;

        transition: .2s all linear;
      }
    }
  }
  &-table{
    &__annotation{
      display: none;
    }
    &__content{
      margin: 14px 0 0 0;
      background: #F8F8F8;
      border-radius: 15px;
    }
    &__row{
      display: grid;
      grid-template-columns: auto 160px 135px 176px 200px;
      gap: 60px;

      padding: 17px 0;
      & + &{
        border-top: 1px solid #D7D7D7;
      }
      &-block{
        padding: 0 32px;
      }
      &-small{
        grid-template-columns: auto 380px 400px;
        gap: 30px;
      }
      &-title{
        background: #FFA319;
        border-radius: 15px;
        height: 64px;

        padding: 0;
      }
      &-block{
        display: flex;
        justify-content: flex-end;
        flex-direction: column;
        height: 100%;
        &-center{
          justify-content: center;
        }
      }
    }
    &__text{
      font-family: 'Gilroy', sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;

      color: #2F2F2F;
      &.trigger{
        &-block{
          display: none;
          &.active{
            display: block;
          }
        }
        &-link{
          display: flex;
          align-items: center;
          justify-content: space-between;
          &::after{
            content: '';
            width: 18px;
            height: 18px;
            display: block;

            background: url("../img/table-arrow.svg") center center/contain no-repeat;
            transform: rotate(180deg);

            transition: .3s all;
          }
          &.active{
            &::after{
              transform: rotate(0);
            }
          }
        }
      }
      &-mobile{
        display: none;
      }
      &-grey{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;
      }
      & + &{
        margin: 24px 0 0 0;
      }
      & + &-mobile{
        margin: 0;
      }
    }
  }
  &-link{
    display: flex;
    align-items: center;

    &::before{
      content: '';
      width: 17px;
      height: 17px;
      display: block;

      background: url("../img/download.svg") center center/contain no-repeat;

      margin: 0 8px 0 0;
    }
  }
  &-bonus{
    &__text{
      text-align: right;
    }
    &__content{
      padding: 18px 14px;

      background: #D9D9D9;

      margin: 13px 0 0 0;
    }
    &__item{
      & + &{
        margin: 10px 0 0 0;
      }
      &_checked{
        .profile-bonus__item-icon{
          &::before{
            content: '';

            position: absolute;
            left: calc(50% - 24px);
            top: calc(50% - 24px);

            width: 48px;
            height: 48px;
            display: block;

            background: url("../img/profile-check.svg") center center/contain no-repeat;
          }
        }
      }
      &-icon{
      width: 70px !important;
      height: 70px !important;

        svg{
          width: 70px!important;
          height: 70px!important;

          circle{
            r:45!important;
          }
        }
        flex-shrink: 0;

        position: relative;

        border-radius: 50%;

        background: #D9D9D9;
      }
      &-wrapper{
        padding: 24px 24px 24px 26px;
        display: flex;
        align-items: center;

        background: #ECECEC;
      }
      &-content{
        margin: 0 0 0 22px;
      }
      &-title{
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;

        color: #000000;
      }
      &-text{
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #000000;

        margin: 8px 0 0 0;
      }
    }
    &__footer{
      margin: 20px 0 0 0;
    }
    &__button{
      font-family: 'Inter',sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      text-align: center;

      color: #FFFFFF;

      height: 40px;
      background: #000000;
      border-radius: 4px;

      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &-header{
    &__wrapper{
      height: 92px;

      background: url("../img/profile-header-bg.svg") center bottom/cover no-repeat;
    }
    &__block{
      & + &{
        margin: 0 0 0 145px;
      }
    }
    &__content{
      display: flex;
      align-items: center;
      justify-content: flex-end;

      padding: 20px 0;
    }
    &__icon{
      display: block;
      width: 18px;
      height: 23px;
    }
    &__nav{
      display: flex;
      align-items: center;
      &-item{
        color:#fff;
        & + &{
          margin: 0 0 0 16px;
        }
        &.active{
          color: #2F2F2F;
          position: relative;
          &::before{
            content: '';
            position: absolute;
            z-index: 1;
            display: block;
            width: 100%;
            height: 60px;
            top: -23.5px;
            left: 0;
            background: #C6D1FF;
            border-radius: 0px 0px 10px 10px;
          }
        }
      }
      &-link{
        display: block;

        padding: 0 16px;

        font-family: 'Graphik LCG', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;

        color: inherit;

        position: relative;
        z-index: 2;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .profile{
    &-table{
      &__row{
        gap: 30px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .profile{
    &-block{
      &__grid{
        grid-template-columns: minmax(0,1fr);
        gap: 16px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .profile{
    &-table{
      &__row{
        &-block{
          display: none;
          &:nth-child(1),&:nth-child(2),&:nth-child(3){
            display: flex;
          }
        }
      }
    }
    &-block{
      &__help{
        position: relative;
        left: 0;
        top: 0;

        margin: 0 0 0 12px;
      }
      &__grid{
        &-content{
          display: flex;
          align-items: center;
        }
        &-item{
          &_row{
            display: flex;
            align-items: center;
          }
        }
      }
    }
    &-checkbox{
      &__small{
        max-width: 230px;
      }
      &__label{
        &-bold{
          font-size: 14px;
          line-height: 17px;
        }
      }
    }
  }
}

@media screen and (max-width: 1366px) {
  .profile{
    &-bonus{
      &__content{
        padding: 0;
        background: none;
      }
      &__button{

        font-family: 'Gilroy', sans-serif;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;

        background: #6776FF;
        box-shadow: 0px 4px 6px rgba(0, 35, 103, 0.08);
        border-radius: 40px;
      }
      &__item{
        display: none;
        &:nth-child(1){
          display: flex;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .profile{
    &-header{
      display: none;
    }
  }
}

@media screen and (max-width: 600px) {
  .profile{
    &-block{
      &__wrapper{
        padding: 24px 16px;
      }
    }
    &-table{
      &__row{
        gap: 0;
        grid-template-columns: auto 90px 80px;
        padding: 16px 0;

        position: relative;
        &-title{
          padding: 15px 0;
        }
        &-block{
          padding: 0 12px;
        }
      }
      &__text{
        font-size: 12px;
        line-height: 15px;
        &-desktop{
          display: none;
        }
        &-mobile{
          display: block;
        }
        &.trigger{
          &-link{
            &::after{
              position: absolute;
              right: 10px;
              top: 15px;
            }
          }
        }
      }
      &__annotation{
        display: block;
        margin: 0 0 24px 0;
        &-text{
          font-family: 'Graphik LCG', sans-serif;
          font-weight: 400;
          font-size: 14px;
          line-height: 17px;

          color: #2F2F2F;
          & + &{
            margin: 8px 0 0 0;
          }
        }
      }
    }
  }
}