.form{
  &-input{
    width: 100%;
    position: relative;
    label{
      position: relative;
    }
    &.error{
      .form-input__item{
        border-color: #FF5F5F;
        background: #FFF4F4;
      }
    }

    &__item{
      -webkit-appearance: none;
      outline: none;

      padding: 24px;

      width: 100%;
      height: 100%;

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      border: 1px solid #C0C6FF;
      border-radius: 12px;

      resize: none;

      &-small{
        height: 52px;

        padding: 17px;
      }
      &-date{
        height: 42px;

        padding: 0 12px;
      }
      &-promt{
        padding-top: 24px;
        padding-bottom: 12px;
      }
      &::placeholder{
        color: #929292;
        opacity: 1;
      }
      &:valid{
        background: #F9F9FF;
      }
      &:focus,&:valid{
        border-color: #6776FF;
         & + .form-input__label{
          font-size: 10px;
          top: -12px;
        }
      }
    }
    &__label{
      position: absolute;
      left: 17px;
      top: calc(50% - 8px);

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #929292;

      transition: .3s all;
    }
    &__help{
      position: absolute;
      right: 16px;
      top: 16px;
    }
    &__error{
      font-family: 'Graphik LCG', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;


      color: #FF5F5F;

      margin: 16px 0 0 0;

    }
    &-date{
      &::after{
        content: '';
        position: absolute;
        right: 8px;
        top: calc(50% - 8px);
        z-index: 1;

        width: 16px;
        height: 16px;
        display: block;

        background: url("../img/arrow-date.svg") center center/contain no-repeat;

        transform: rotate(90deg);

        transition: .1s all;
      }
      &.active{
        &::after{
          transform: rotate(-90deg);

        }
      }
    }
  }
}