.calendar{
  &-header{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;

      padding: 11px;

      background: #FFA319;
      border-radius: 90px;
    }
  }
  &__content{
    margin: 16px 0 0 0;

  }
  &__arrow{
    width: 130px;
    height: 60px;

    padding: 0 24px;

    display: flex;
    align-items: center;

    background: rgba(255, 255, 255, 0.4);
    border-radius: 90px;
    transition: .3s all linear;
    &:hover{
      padding: 0 12px;
      cursor: pointer;
    }
    &::before{
      content: '';

      width: 29px;
      height: 29px;
      display: block;

      background: url("../img/arrow-calendar.svg") center center/contain no-repeat;

    }
    &-right{
      justify-content: flex-end;
      &::before{
        transform: rotate(180deg);
      }
    }
  }
  &__title{
    font-family: 'Gilroy', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;

    color: #FFFFFF;

    display: flex;
    align-items: center;

    span + span{
      margin: 0 0 0 5px;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(7,minmax(0,1fr));

    border: 1px solid #BEC5FF;
    border-radius: 30px;
  }
  &__day{
    position: relative;

    min-height: 120px;

    padding:8px 6px;

    border-bottom: 1px solid #BEC5FF;

    & + &{
      border-left: 1px solid #BEC5FF;
      &:nth-child(7n + 1){
        border-left: none;
      }
    }

    &:nth-last-child(-n + 7){
      border-bottom: none;
    }


    &-today{
      background: #FFBD59;
      &::before{
        content: 'cегодня';
        position: absolute;
        right: 6px;
        top: 8px;

        font-family: 'Gilroy', sans-serif;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;

        color: #2F2F2F;
      }
    }
  }
  &__week{
    min-height: 60px;
    max-height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__text{
    font-family: 'Gilroy', sans-serif;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;

    color: #2F2F2F;
  }
  &__empty{
    background: #FFFAF2;
  }
  &__event{
    position: relative;
    z-index: 2;

    border-radius: 20px;
   &_purple{
     background: #EAE0FF;
     border-color:#EAE0FF;
   }
    &_yellow{
      background: #FFE9B1;
      border-color: #FFE9B1;
    }
    &_green{
      background: #C6FFB2;
      border-color: #C6FFB2;
    }
    &_cyan{
      background: #C9F8FF;
      border-color: #C9F8FF;
    }
    &:hover{
      .calendar__event-content{
        opacity: 1;
        z-index: 2;
      }
    }
    &-text{
      font-family: 'Graphik LCG' ,sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 10px;

      color: #2F2F2F;

      white-space: nowrap;
      overflow: hidden;

      display: block;

      text-overflow: ellipsis;

      padding: 6px 16px;
      background: inherit;
      box-shadow: 0px 0px 4px 1px transparent;
      border-radius: 20px;

      transition: .3s all linear;

      position: relative;
      z-index: 3;

      span{
        font-weight: 600;
      }
      &:hover{
        box-shadow: 0px 0px 4px 1px rgba(7, 14, 77, 0.2);
      }
    }
    &-content{
      position: absolute;
      bottom: 8px;
      left: 0;

      background: #FFFFFF;
      border: 2px solid;
      border-color: inherit;
      box-shadow: 0px 2px 10px rgba(7, 14, 77, 0.2);
      border-radius: 20px 20px 0px 0px;

      max-width: 355px;
      min-height: 255px;

      opacity: 0;
      z-index: -1;

      transition: .3s all linear;
    }
  }
  &__link{
    display: flex;
    align-items: center;

    font-family: 'Gilroy', sans-serif;
    font-weight: 500;
    font-size: 14px;

    color: #6776FF;

    position: absolute;
    right: 6px;
    bottom: 4px;
    &::after{
      content: '';
      width: 12px;
      height: 12px;
      display: block;

      flex-shrink: 0;
      background: url("../img/arrow-down-blue.svg") center center/contain no-repeat;

      margin: 0 0 0 8px;
    }
    &.active{
      &::after{
        transform: rotate(180deg);
      }
    }
  }
}