.header {
  &__wrapper {
    background: #FFFFFF;
    box-shadow: 0px 4px 20px 2px rgba(0, 52, 153, 0.05);
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 64px;
  }

  &__block {
    display: flex;
    align-items: center;
  }

  &__logo {
    width: 118px;
    height: 34px;
  }

  &__profile {
    display: flex;
    align-items: center;

    margin: 0 0 0 115px;

    &-block {
      & + & {
        margin-left: 32px;
      }

      &_burger {
        display: none;
      }
    }
  }

  &-nav {
    &__list {
      display: flex;
      align-items: center;

      &-mobile {
        display: none;
      }
    }

    &__divider {
      display: none;
    }

    &__item {
      & + & {
        margin-left: 48px;
      }
    }

    &__link {
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;

      &.active {
        position: relative;

        &::after {
          content: '';
          position: absolute;
          left: -2px;
          bottom: -3px;

          width: calc(100% + 4px);
          height: 2px;

          background: #6776FF;
        }
      }

    }
  }

  &-profile {
    &__wrapper {
      position: relative;
    }

    &__link {
      display: flex;
      align-items: center;

      &::after {
        content: '';
        width: 16px;
        height: 16px;
        display: block;
        margin-left: 8px;

        background: url("../img/arrow-blue.svg") center center/contain no-repeat;

        transition: .3s rotate;
      }

      &.active {
        &::after {
          transform: rotate(180deg);
        }
      }

      &--active {
        position: relative;

        &::after {
          display: none;
        }

        &::before {
          content: '';
          position: absolute;
          z-index: 1;
          bottom: -21px;
          left: calc(50% - 41px);
          width: 82px;
          height: 53px;
          display: block;

          background: url("../img/nav-active-square.svg") center center/contain no-repeat;
        }

        svg{
          path{
            fill: #fff;
          }
        }
      }

      &-icon {
        display: block;
        width: 18px;
        height: 22px;

        position: relative;
        z-index: 2;
      }
    }

    &__list {
      position: absolute;
      right: 0;
      top: calc(100% + 11px);

      opacity: 0;
      z-index: -1;

      transition: .5s linear;

      background: #FFFFFF;
      border-radius: 6px;
      overflow: hidden;

      width: 210px;

      box-shadow: 0px 2px 15px rgba(7, 14, 77, 0.1);

      &.active {
        opacity: 1;
        z-index: 10;
      }

      &-item {
        border-bottom: 1px solid #E3E3E3;

        transition: .3s all linear;

        &:last-child {
          border-bottom: none;
        }

        &:hover {
          background: #F9F9FF;
          border-color: #6776FF;
        }
      }

      &-link {
        display: flex;
        align-items: center;
        padding: 12px 24px;

        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;

        &_enter {
          font-weight: 500;
          color: #6776FF;

          &::before {
            content: '';
            width: 16px;
            height: 16px;
            display: block;
            margin-right: 8px;

            background: url("../img/enter.svg") center center/contain no-repeat;
          }
        }
      }
    }
  }
}

.logo {
  width: 100%;
  height: 100%;

  &__img {
    width: 100%;
    height: 100%;

    object-fit: contain;
  }
}

.notification {
  position: relative;

  &-close {
    width: 32px;
    height: 32px;
    display: block;

    background: url("../img/close-popup.svg") center center/contain no-repeat;
  }

  &__icon {
    display: block;
    width: 18px;
    height: 21px;

    &_new {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        right: -3px;
        top: -2px;

        width: 12px;
        height: 12px;
        display: block;

        background: #6776FF;
        border: 1px solid #FFFFFF;
        border-radius: 50%;
      }
    }
  }

  &__content {
    position: absolute;
    right: 0;
    top: 35px;

    opacity: 0;
    z-index: -1;
    display: none;

    transition: .3s all linear;

    background: #FFFFFF;
    box-shadow: 0px 2px 15px rgba(7, 14, 77, 0.1);
    border-radius: 20px;

    width: 715px;

    &.active{
      display: block;
      opacity: 1;
      z-index: 10;
    }
  }

  &-content {
    &__close {
      position: absolute;

      right: 10px;
      top: 10px;
      display: none;
    }

    &__wrapper {
      padding: 27px 40px;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__block {
      margin: 14px 0 0 0;

      padding: 0 24px 0 0;

      max-height: 520px;

      .swiper-scrollbar{
        background: linear-gradient(0deg, #EEEEEE, #EEEEEE), #EEEEEE;
        &-drag{
          background: #2F2F2F;
        }
      }
    }
  }

  &__item {
    & + & {
      margin: 6px 0 0 0;
    }
  }

  &-item {
    &_undread {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        left: 8px;
        top: 8px;

        width: 10px;
        height: 10px;
        display: block;

        border-radius: 50%;

        background: #6776FF;
      }
    }

    &__wrapper {
      background: #F8F8F8;
      border-radius: 10px;

      padding: 24px 24px 16px 24px;
    }

    &__header {
      &-block {
        display: flex;
        align-items: center;
        justify-content: space-between;

        & + & {
          margin: 6px 0 0 0;
        }
      }
    }

    &__content {
      margin: 12px 0 0 0;
    }

    &__footer {
      margin: 16px 0 0 0;

      display: flex;
      justify-content: flex-end;
    }

    &__text {
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #2F2F2F;
    }

    &__title {
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;

      color: #2F2F2F;

      a {
        color: #6776FF;
      }
    }

    &__link {
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 19px;

      color: #6776FF;

      display: flex;
      align-items: center;

      &::after {
        content: '';

        width: 22px;
        height: 22px;
        display: block;

        flex-shrink: 0;

        background: url("../img/arrow-blue-left.svg") center center/contain no-repeat;

        margin: 0 0 0 6px;
      }
    }
  }
}

.burger {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 15px;
  height: 12px;

  &__item {
    width: 100%;
    height: 2px;
    display: block;

    border-radius: 4px;

    background-color: #6776FF;

    transition: .5s all linear;
  }

  &.active {
    width: 16px;
    height: 16px;

    position: relative;

    .burger {
      &__item {
        position: absolute;

        &:nth-child(2) {
          opacity: 0;
        }

        &:nth-child(1) {
          left: 0;
          top: 8px;
          transform: rotate(45deg);
        }

        &:nth-child(3) {
          right: 0;
          top: 8px;
          transform: rotate(-45deg);
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;

    &__nav {
      position: fixed;
      width: 100%;
      top: 64px;
      left: 0;
      z-index: 99;

      background: #FFFFFF;
      box-shadow: 0px 2px 15px rgba(7, 14, 77, 0.1);
      border-radius: 0px 0px 20px 20px;

      padding: 24px 0 40px 0;

      display: none;

      max-height: calc(100vh - 64px);
      overflow-x: hidden;
      overflow-y: auto;

      &.active {
        display: block;
      }
    }

    &__profile {
      &-block {
        &_profile {
          display: none;
        }

        &_burger {
          display: block;
        }
      }
    }

    &-nav {
      max-width: 295px;

      margin: 0 auto;

      &__divider {
        display: flex;
        align-items: center;
        justify-content: center;

        margin: 32px 0 8px 0;

        background: #C6D1FF;
        height: 50px;
        border-radius: 20px;

        &-icon {
          width: 21px;
          height: 25px;
        }
      }

      &__list {
        flex-direction: column;

        &-mobile {
          display: flex;
        }
      }

      &__item {
        width: 100%;

        & + & {
          margin: 8px 0 0 0;
        }

        & + &_enter {
          margin: 25px 0 0 0;
        }
      }

      &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 2px solid #6776FF;
        border-radius: 20px;
        height: 58px;
        width: 100%;

        &_blue {
          background: #6776FF;
          color: #fff;
        }

        &_enter {
          height: auto;
          justify-content: flex-end;
          font-weight: 500;

          border: none;
          color: #6776FF;

          &::before {
            content: '';
            width: 22px;
            height: 22px;
            display: block;

            margin-right: 6px;

            background: url("../img/enter.svg") center center/contain no-repeat;
          }
        }

        &::after {
          display: none;
        }
      }
    }
  }
  .notification {
    &__content {
      width: 100vw;
      position: fixed;
      top: 80px;
    }

    &-content {
      &__close {
        display: block;
      }

      &__wrapper {
        padding: 40px 24px;

      }

      &__link {
        max-width: 105px;
      }

      &__title {
        font-size: 20px;
        line-height: 30px;
      }

      &__header{
        padding: 0 40px 0 12px;
      }

      &__block {
        max-height: calc(100vh - 300px);

      }
    }

    &-item {
      &__header {
        &-block {
          flex-direction: column-reverse;
          align-items: flex-start;
        }
      }

      &__date {
        margin: 0 0 16px auto;
      }

      &__wrapper {
        padding: 12px 16px;
      }

      &__date {
      }
    }
  }
}