.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;


  z-index: -1;
  background: rgba(#000,.45);


  opacity: 0;

  transition: .5s all linear;
  &.active{
    opacity: 1;
    z-index: 100;
  }
  &-login{
    &__content{
      margin: 11vh auto 0 auto;
      max-width: 635px;
      width: 100%;
      background: #FFFFFF;
      box-shadow: 0px 0px 10px rgba(7, 14, 77, 0.1);
      border-radius: 20px;

      padding: 56px 24px 64px 24px;

      position: relative;
    }
    &__desc{
      margin: 8px 0 0 0;


      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #2F2F2F;
      &-center{
        text-align: center;
      }
    }
    &__wrapper{
      max-width: 315px;
      width: 100%;

      margin: 0 auto;
      &-big{
        max-width: 365px;
      }
    }
    &__close{
      position: absolute;
      right: 24px;
      top: 24px;
    }
  }
  &-favorite{
    &-list{
      counter-reset: favorite;
      li + li{
        margin: 12px 0 0 0;
      }
      li{
        display: flex;
        align-items: center;
        counter-increment: favorite;
        &::before{
          content: counter(favorite);

          width: 28px;
          height: 28px;
          display: flex;
          align-items: center;
          justify-content: center;

          border-radius: 50%;
          background: #6776FF;

          flex-shrink: 0;

          margin: 0 16px 0 0 ;

          font-family: 'Gilroy', sans-serif;
          font-weight: 700;
          font-size: 16px;
          line-height: 28px;

          color: #FFFFFF;
        }
      }
    }
    &__content{
      margin: 11vh auto 0 auto;
      max-width: 850px;
      width: 100%;
      background: #FFFFFF;
      box-shadow: 0px 0px 10px rgba(7, 14, 77, 0.1);
      border-radius: 20px;

      padding: 56px 24px 64px 56px;

      position: relative;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 10px;
    }
    &__close{
      position: absolute;

      right: 24px;
      top: 24px;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #2F2F2F;

      a{
        font-size: 16px;
      }
    }
    &__desc{
      margin: 6px 0 0 0;
      &-small{
        max-width: 325px;
      }
    }
    &__block{
      margin: 24px 0 0 0;
    }
    &__list{
      margin: 12px 0 0 0;
    }
    &__img{
      width: 100%;
      height: 100%;

      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;

        &_m{
          display: none;
        }
      }
    }
  }
}

.login{
  &-form{
    &__checkbox{
      & + &{
        margin: 12px 0 0 0;
      }
    }
    &__content{
      margin: 24px 0 0 0;
      &-small{
        margin: 16px 0 0 0;
      }
    }
    &__input{
      & +& {
         margin: 16px 0 0 0;
      }
    }
    &__footer{
      margin: 32px 0 0 0;
      &-big{
        margin: 40px 0 0 0;
      }
    }
    &__register{
      margin: 24px 0 0 0;
    }
    &__img{
      width: 100%;
      height: 210px;

      display: flex;
      align-items: center;
      justify-content: center;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #000000;

      & + &{
        margin: 8px 0 0 0;
      }
    }
    &__controls{
      margin: 8px 0 0 0;

      display: flex;
      align-items: center;
      justify-content: center;
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        color: #2F2F2F;
      }
    }
    &__pagination{
      position: relative;
      width: 120px;
      height: 7px;
      display: block;

      margin: 0 0 0 8px;
      .swiper-pagination{
        background: #F1F1F1;
        border-radius: 40px;
        overflow: hidden;
        &-progressbar-fill{
          background: #FFA319;
          border-radius: 40px;
        }
      }
    }
    &__back{
      position: absolute;
      left: 24px;
      top: 24px;

      width: 32px;
      height: 32px;
      display: block;
      svg{
        rect{
          transition: .3s fill;
        }
        path{
          transition: .3s stroke;
        }
      }
      &:hover{
        cursor: pointer;

        svg{
          rect{
            fill: #6776FF;
          }
          path{
            stroke:#fff;
          }
        }
      }
    }
  }
}

.popup{
  &-close{
    svg{
      rect,path{
        transition: .3s fill linear;
      }
    }
    &:hover{
      cursor: pointer;

      svg{
        rect{
          fill:#6776FF;
        }
        path{
          fill: #fff;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .login{
    &-form{
      &__footer{
        &-big{
          margin: 32px 0 0 0;
        }
      }
      &__back{
        top: 12px;
        left: 16px;
      }
    }
  }
  .popup{
    &-login{
      &__close{
        top: 12px;
        right: 16px;
      }
    }
    &-favorite{
      &__content{
        padding: 48px 24px;
      }
      &__title{
        text-align: center;
      }
      &__grid{
        grid-template-columns: minmax(0,1fr);
        gap: 24px;
      }
      &__img{
        max-height: 130px;
        display: flex;
        align-items: center;
        justify-content: center;
        &-item{
          display: none;
          &_m{
            display: block;
          }
        }
      }
    }
  }
}